/* GLOBAL STYLES */
html {
  font-size: 62.5%;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 2rem;
  text-align: center;
  background: #fff;
  color: #255683;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.App {
  min-height: 100vh;
  position: relative;
  padding-bottom: 50px;
}

header {
  height: 200px;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(25%, #255683), color-stop(25%, #397da2), color-stop(50%, #397da2), color-stop(50%, #8fd4e5), color-stop(75%, #8fd4e5), color-stop(75%, #d5f0f2));
  background: linear-gradient(to bottom, #255683 25%, #397da2 25%, #397da2 50%, #8fd4e5 50%, #8fd4e5 75%, #d5f0f2 75%);
}

footer {
  background: #255683;
  color: #fff;
  padding: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

footer p {
  margin: 0;
  font-size: 1.6rem;
}

a {
  color: #fff;
  border-bottom: 2px solid #ce587d;
  text-decoration: none;
  font-weight: bold;
  -webkit-transition: 0.3s all;
  transition: 0.3s all;
}

a:hover, a:focus {
  color: #8fd4e5;
}

.wrapper {
  max-width: 1200px;
  width: 85%;
  margin: 0 auto;
}

@media (max-width: 700px) {
  .wrapper {
    width: 95%;
  }
}

.flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

h1 {
  margin: 0;
  font-size: 4rem;
  letter-spacing: 2px;
  padding-top: 50px;
  color: #fff;
  text-shadow: 2px 2px #ce587d;
}

h2 {
  font-size: 3rem;
  letter-spacing: 2px;
  margin-top: 10px;
  color: #c23864;
  text-shadow: 2px 2px #fff;
}

h3 {
  margin-bottom: 0;
  font-size: 24px;
  display: inline-block;
  border-bottom: 3px solid #ce587d;
}

h4 {
  margin: 0;
}

label {
  display: block;
  margin-top: 15px;
  text-align: left;
  margin-bottom: 5px;
}

button {
  display: block;
  border: 0;
  background: #255683;
  color: #fff;
  margin: 20px auto;
  height: 48px;
  padding: 10px 25px;
  -webkit-transition: 0.3s all;
  transition: 0.3s all;
  cursor: pointer;
}

button:hover, button:focus {
  background: #ce587d;
}

.alternate-button {
  background: #397da2;
}

.input-button {
  font-size: smaller;
  background: #fff;
  border: 1px solid #fff;
  color: #c23864;
  margin: 0 auto;
}

.input-button:hover, .input-button:focus {
  background: #fff;
  border: 1px solid #c23864;
  color: #c23864;
}

.input-button__add {
  margin-top: 5px;
}

.input-button__remove {
  margin-left: 10px;
  height: 100%;
  padding: 17px 25px;
}

@media (max-width: 480px) {
  .input-button__remove {
    padding: 17px 15px;
  }
}

form {
  padding: 20px 20px 0;
  margin: 0 auto;
  width: 65%;
}

@media (max-width: 769px) {
  form {
    width: 100%;
  }
}

input,
button {
  border-radius: 5px;
}

input {
  width: 100%;
  border: 2px solid #397da2;
  padding: 15px;
  color: #255683;
}

input::-webkit-input-placeholder {
  color: #397da2;
  opacity: 0.7;
}

input:-ms-input-placeholder {
  color: #397da2;
  opacity: 0.7;
}

input::-ms-input-placeholder {
  color: #397da2;
  opacity: 0.7;
}

input::placeholder {
  color: #397da2;
  opacity: 0.7;
}

input:hover, input:focus {
  border: 2px solid #ce587d;
}

.name-input {
  margin: 5px 0;
}

.person-component {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

@media (max-width: 480px) {
  .person-component-input {
    width: calc(100% - 104px);
  }
}

.form-error {
  margin-bottom: 0;
  color: #ce587d;
}

/* STYLES FOR ITEM FORM */
.item-form {
  position: relative;
  margin-bottom: 50px;
}

