  margin-right: 5px;
  margin-left: 0;
}

.name-label {
  color: #397da2;
  border-radius: 5px;
  font-size: 1.8rem;
  display: inline-block;
  margin-right: 20px;
}

.name-label:hover,
.name-label:focus {
