/* STYLING FOR ALL BILL/ITEMS CARDS */
.card-display {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}

.card {
  border-radius: 10px;
  overflow: hidden;
  border: 0;
}

.card ul {
  width: 100%;
}

.card li {
  width: 100%;
  padding: 20px;
}

@media (max-width: 480px) {
  .card li {
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .card p {
    font-size: 1.8rem;
  }
}

.card li:nth-of-type(2n) {
  background: #8fd4e5;
}

.bill-card-bottom,
.card li:nth-of-type(2n + 1) {
  background: #d5f0f2;
}

.card-heading {
  margin: 0;
  width: 100%;
  padding: 20px;
  background: #255683;
  color: #fff;
  font-weight: bold;
}

.card-left,
.card-right {
  display: inline-block;
  margin: 0;
}

.card-left {
  width: 60%;
  text-align: left;
}

.card-right {
  width: 40%;
  text-align: right;
}

.card-strong {
  font-weight: bold;
}

.card-small {
  font-size: 1.7rem;
  margin-top: 5px;
}

.invoice-card,
.items-card,
.bill-card {
  max-width: 500px;
}

.invoice-card,
.bill-card {
  margin: 20px auto;
  width: calc(50% - 40px);
}

@media (max-width: 700px) {
  .invoice-card,
  .bill-card {
    width: 100%;
  }
}

.items-card {
  margin: 30px auto 50px auto;
  width: 80%;
}

@media (max-width: 700px) {
  .items-card {
    width: calc(100% - 40px);
  }
}

.bill-card {
  cursor: pointer;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.bill-card:hover .card-heading,
.bill-card:focus .card-heading {
